body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont,
  "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans",
  "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  transition:         all 0.37s cubic-bezier(0.645, 0.045, 0.355, 1);;
  -webkit-transition: all 0.37s cubic-bezier(0.645, 0.045, 0.355, 1);;
  -moz-transition:    all 0.37s cubic-bezier(0.645, 0.045, 0.355, 1);;
  -ms-transition:     all 0.37s cubic-bezier(0.645, 0.045, 0.355, 1);;
  -o-transition:      all 0.37s cubic-bezier(0.645, 0.045, 0.355, 1);;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
