@import './main';

.container {
  background-color: $white;

  color: $black;
  font-size: 24px
}

.header {
  padding: $vert-space $side-space;
}

.nav-logo {
  text-decoration: none;
  color: $black;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 6vh;
}

.nav-logo:hover {
  color: $blue;
}


.nav-link {
  margin: 0;
  padding: 0;
  float: right;
}

li {
  list-style-type: none;
  display: inline;
  margin: 0;
}

.nav-link a {
  text-decoration: none;
  color: $black;
  padding: 0px 8px;

}

.nav-link a:hover {
  color: $blue;
}


a.nav-link-active {
  /* color: deeppink; */
}

