
/*

  Constants

  - spaces
    - global side space

*/

$side-space: 24px;
$vert-space: 24px;

/*

  Colors

  - colors
  - selectors
    - text
    - background

*/

/* colors */

$primary-color: deeppink;

$black: #111111;
$white: #FFFFFF;
$silver: #DDDDDD;
$grey: #AAAAAA;
$deepgrey: #777777;

$blue: #0074D9;
$pblue: #c5e9e7;

/* selectors */
.black { color: $black; }
.white { color: $white; }
.silver { color: $silver; }
.grey { color: $grey; }
.deepgrey { color: $deepgrey; }

.blue { color: $blue; }
.pblue { color: $pblue; }

/* background */
.bg-black { background-color: $black; }
.bg-white { background-color: $white; }
.bg-silver { background-color: $silver; }
.bg-grey { background-color: $grey; }
.bg-deepgrey { background-color: $deepgrey; }

.bg-blue { background-color: $blue; }
.bg-pblue { background-color: $pblue; }

/*

  Mixins

  - common

*/

/* common */
@mixin sector {
  padding: 12px $side-space;
  font-weight: 600;
  line-height: 1.45em;
}

